@import "./scss/functions.scss";

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  color: map-get($main_colors, white_accent);
  font-family: "Montserrat", "Roboto";
  background-color: color(white_accent);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

p {
  font-family: map-get($main_fonts, body);
  font-weight: map-get($main_weights, body);
  color: map-get($main_colors, headers);
  font-size: 1em;
}

strong {
  font-family: font(headers);
  font-size: size(secondary_header);
  font-weight: weight(medium);
  color: color(primary);
}

blockquote {
  font-family: map-get($main_fonts, secondary);
  font-size: map-get($main_font-size, body);
  color: map-get($main_colors, secondary);
  font-weight: 500;
  font-style: italic;
}

.submit,
button {
  font-family: map-get($main_fonts, body);
  font-weight: map-get($main_weights, medium);
  border-radius: 30px;
  background-color: map-get($main_colors, white_accent);
  color: map-get($main_colors, headers);
  border-color: map-get($main_colors, primary);
  border-width: 1px;
}

h1 {
  margin-top: 0;
  color: color(primary);
}

h2 {
  font-family: map-get($main_fonts, headers);
  font-weight: map-get($main_weights, headers);
  font-size: map-get($main_font-size, secondary_header);
  color: map-get($main_colors, headers);
}

h3 {
  color: color(secondary);
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.card {
  // height: 414px;
  // width: 310px;
  text-align: center;
  box-shadow: 2px 3px 10px map-get($main_colors, secondary);
  border-radius: 10px;
  padding-top: 0.1em;
  margin: 2em 0;
  background-color: color(white_accent);
}

.wrapper {
  padding: 1.5em;
  max-width: 1366px;
  margin: 0 auto;
}

form {
  margin: 2em 0 2em 0;

  label {
    font-family: map-get($main_fonts, body);
    font-weight: map-get($main_weights, headers);
    color: map-get($main_colors, headers);
    font-size: 1em;
    margin: 1em 1em 0.5em 0;
  }

  h4 {
    max-width: 150px;
  }
  
  input {
    border-radius: 10px;
    border-width: 1px;
    border-color: map-get($main_colors, secondary);
    margin: 0.5em 0 1em 0;
    padding: 0.5em;
    font-family: map-get($main_fonts, primary);
  }

  // .submit {
  //   border-radius: 30px;
  //   padding: 12.5px 10px 12.5px 10px;
  //   background-color: color(primary);
  //   color: color(white_accent);
  // }
}

.form-error-message {
  color: red;
  font-size: 0.7em;
  margin: 0 0 0.8em 0;
}

.placeholder {
  text-align: center;
}
